import React from 'react';
import twemoji from 'twemoji';
import styled from 'styled-components';

interface IRenderedEmojiProps {
    value: string;
  }


  // there is little difference between the TodoBox and this - can I maybe extend the todoBox styles somehow?
const StyledSpanEmoji = styled.span`

    display: none;
  

@media print
{ 
    
    display: inline-block;
    width: auto;

    overflow: hidden;

}
`;
    
export const RenderedEmoji = (props: IRenderedEmojiProps) => {
  
    return (
        <StyledSpanEmoji
          dangerouslySetInnerHTML={{
            __html: twemoji.parse(props.value, {
              folder: 'svg',
              ext: '.svg',
              size: 35
            })
          }}
        />
      )
    
}
