import React from 'react';
import styled from 'styled-components';
import { CustomCheckbox, TodoBox } from './styles/todoStyles';
import { ChangeEvent, useState} from 'react';


import { RenderedEmoji } from './RenderedEmoji';
import { EmojiPicker } from './EmojiPicker';

const CancelButton = styled.button`

  @media print { 
      display: none !important;
    }

  border: none;
  background: inherit;
  position: relative;
  height: 1.5em;
  width: 1.5em;
  cursor: pointer;
`;




export interface TODO {
    done: boolean;
    title: string;
    key: string;
    //isEdit: boolean;
    newIndex: number;
}

interface ITodoProps {
    index: number;
    todo: TODO;
    deleteTodo?(todo: TODO): void;
    changeTodo?(todo: TODO): void;
}

interface ITodoState {
}


export const ToDo = (props: ITodoProps, state: ITodoState) => {
    const textRef = React.createRef<HTMLInputElement>();

    

    const addEmoji = (data: any): void => {

        const updatedTodo: TODO = { ...props.todo };
        updatedTodo.title += data.native;

        if (props.changeTodo) {
            props.changeTodo(updatedTodo);
        }

        //setEmojiState(false);

        if (textRef.current) {
            textRef.current.focus();
        }

    }

    const handleChange = (event: ChangeEvent<HTMLInputElement>) => {
        // 1. get current state and copy it
        const updatedTodo: TODO = { ...props.todo };


        // 2. update the copy of the current state
        if (event.target.type === 'checkbox') {
            updatedTodo.done = event.target.checked;
        } else if (event.target.name === "title") {
            updatedTodo.title = event.target.value;
            //updatedTodo.isEdit = false;

            //console.log("edited", props.todo.isEdit);
        }
        /*
        else {
             updatedTodo[event.target.name] = event.target.value;
        }*/
        // 3. write the update to state
        if (props.changeTodo) {
            props.changeTodo(updatedTodo);
        }
    }


    /*
    
    handleClick = (e: any) => {
        //e.stopPropagation();
  
       console.log("clicked", props.todo.isEdit);
        
        // 1. get current state and copy it
        const updatedTodo: TODO = { ...props.todo };
        updatedTodo.isEdit = true;
        if (props.changeTodo) {
            props.changeTodo(updatedTodo);
        }
    }
    */


    /*<img src="/images/icon-check.svg" alt="" />*/


    const [refresh, doRefresh] = useState(false);
    const onMouseLeave = () => {

        doRefresh(!refresh);
    }

    return (
        <TodoBox done={props.todo.done} index={props.index} onMouseLeave={onMouseLeave}>

            <CustomCheckbox index={props.index}>
                <input type="checkbox" name={props.todo.key} id={props.todo.key} checked={props.todo.done} data-testid="todo-checkbox" onChange={handleChange} />

                <label htmlFor={props.todo.key}>
                    <span>âœ“</span>
                </label>
            </CustomCheckbox>
            <input type="text" ref={textRef} name="title" id="todo" data-testid="todo-title" value={props.todo.title} onChange={handleChange}
                style={{ maxWidth: (props.todo?.title?.length * 0.7) + "em" }} />

            <RenderedEmoji value={props.todo.title} />

            <EmojiPicker addEmoji={addEmoji} refresh={refresh}/>

            <CancelButton data-testid="cancel-todo" onClick={() => {
                if (props.deleteTodo) {
                    props.deleteTodo(props.todo);
                }
            }}>

                <img src='images/icon-cross.svg' alt="cancel"></img>
            </CancelButton>
        </TodoBox>
    )

}
