import styled from 'styled-components';
import {Theme} from './theme';

interface CustomCheckboxProps {
  index: number;
  theme: Theme;
}

export const CustomCheckbox = styled.div<CustomCheckboxProps>`

  position: relative;
  height: 1.3em;
  width: 0.8em;

  label {
    border: 1px solid ${props => {
      if (props.index < 3) {
        return "rgb(255, 105, 105);";
      } else if (props.index < 6) {
        return "rgb(171 116 0);";
      } else {
        return "rgb(71 89 131);"
      }
    }}
    border-radius: 25%;
    border-width: ${props => {
      if (props.index < 3) {
        return "0.1em;";
      } else if (props.index < 6) {
        return "0.12em;";
      } else {
        return "0.12em;"
      }
    }}
    cursor: pointer;
    height: 1.2em;
    left: 0;
    position: absolute;
    top: 0;
    width: 1.2em;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  label:hover {
    border-color2: ${props => props.theme.optionsHover};
  }

  label img {
    opacity: 0;
    width: 55%;
  }

  input[type="checkbox"] {
    visibility: hidden;
  }

  input[type="checkbox"]:checked + label {
    background2: linear-gradient(120deg, #57ddff, #c058f3);
    border2: none;
  }

  
  input[type="checkbox"] + label span {
    display: none;
  }
  
  input[type="checkbox"]:checked + label span {
    margin-top: 0.2em;
    display: block;

  }

  input[type="checkbox"]:checked + label img {
    opacity: 1;
  }

`;


interface TodoBoxProps {
  done: boolean;
  index: number;
}

export const TodoBox = styled.li<TodoBoxProps>`
  background-color: ${props => props.theme.todoBackgroundColor};
  
  color: ${props => {
    if (props.index < 3) {
      return "rgb(255, 105, 105);";
    } else if (props.index < 6) {
      return "rgb(171 116 0);";
    } else {
      return "rgb(71 89 131);"
    }
  }}
 
  padding: ${props => {
    /* top | right | bottom | left */
    if (props.index < 3) {
      return "0.7em 0.7em 0.7em 1em";
    } else if (props.index < 6) {
      return "0.7em 0.7em 0.7em 1.6em";
    } else {
      return "0.8em 0.7em 0.8em 2.0em";
    }
  }};
  grid-gap: 1em;
  display: grid;
  grid-template-columns: auto minmax(0, 1fr) 20px 20px;
  align-items: center;
  justify-content: center;
  border-bottom: 2px dotted hsl(236deg 3% 81%);

  font-size: ${props => {
    if (props.index < 3) {
      return "30px";
    } else if (props.index < 6) {
      return "22px";
    } else {
      return "18px";
    }
  }};

  .emoji {
    vertical-align: sub;
      margin-left: 3px;
      margin-right: 3px;

      height: ${props => {
        if (props.index < 3) {
          return "30px";
        } else if (props.index < 6) {
          return "22px";
        } else {
          return "18px";
        }
      }};
      width: ${props => {
        if (props.index < 3) {
          return "30px";
        } else if (props.index < 6) {
          return "22px";
        } else {
          return "18px";
        }
      }};
  }

  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
    -o-transition: all 0.3s;
       transition: all 0.3s;


  
  input {

    min-width: 150px;
   
    @media print { 
      display: none !important;
    }

    font: inherit;
    background: inherit;
    
    color: inherit;
    border: none;
    

    
    text-decoration: ${props => props.done && 'line-through'};
  }


  @media (max-width: 650px) {
    
  }
`;
//color: ${props => props.done ? props.theme.todoTextLineThrough : props.theme.todoTextColor};