import styled from 'styled-components';
//import {Theme} from './styles/theme';

const StyledHeader = styled.div`

  @media print { display: none !important; }
    
  display: flex;
  justify-content: space-between;
  align-content: center;

    h1 {
        color: ${props => props.theme.headerColor};
        font-weight: 700;
        text-transform: uppercase;
        font-size: 2rem;
        letter-spacing: 0.3em;
        margin: 1em 0;
    }

    button {
        display: flex;
        align-items: center;
        justify-content: center;
        align-self: center;

        height: 30px;

        img { 
            height: 30px;
            margin-right: 5px;
        }

        span {
            border-bottom: dotted 1px #f6f6ff;
            color: #f6f6ff;
            font-weight: bold;
        }

    }

    .theme-changer {
        border: none;
        background: transparent;
        cursor: pointer;
    }

    /* img {
        width: 100%;
    } */
`;



export const ListHeader = (props: any) => {
    const printPage = () => {
        window.print();
    };


    return(
        <StyledHeader className="header">
            <h1>Print your tasks</h1>
            <button className="theme-changer" onClick={printPage}>
                <img alt="theme-changer" src="./logo192.png">
                </img>
                <span>Print</span>
            </button>
        </StyledHeader>
    )
  
}
