import styled, { ThemeProvider } from 'styled-components';
import { DragDropContext, Droppable, Draggable, DropResult, DragUpdate, ResponderProvided } from 'react-beautiful-dnd';
import GlobalStyle from './styles/globalStyles';
import { lightTheme, /*darkTheme,*/ Theme } from './styles/theme';
import InputForm from './InputForm';
import { TODO, ToDo } from './ToDo';
import { Filter, Options } from './Options';
import { ListHeader } from './ListHeader';
import { useStickyState } from "./storage/LocalStorage";
import { useEffect } from 'react';

const TodoWrapper = styled.div`
    max-width: 800px;
    position: relative;
    margin: auto;

    @media (max-width: 650px) {
        padding: 0 2rem;
        top: 1.5rem;
    }
`;

const TodoList = styled.ul`

    box-shadow: 10px 10px 20px ${props => props.theme.shadowColor};
    div:first-child li{
      border-top-left-radius: 0.3em;
      border-top-right-radius: 0.3em;
    }

    @media (max-width: 650px) {
        box-shadow: none;
    }
`;

const BackgroundHeader = styled.div`

@media print { display: none !important; }

  width: 100vw;
  position: absolute;
  top: 0;
  height: 30vh;
  background: ${props => props.theme.mainBackground} center center/cover;

  @media (max-width: 650px) {
      content: ${props => props.theme.mobileBackground};
      max-width: 100vw;
  }
`;

const Footer = styled.footer`

@media print
{    
    display: none !important;
}

    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 1.5em 0;
    color: ${props => props.theme.optionsColor};
`;

export interface State {
    //todos: TODO[];
    filter: Filter;
    currentTheme: Theme;
}

const inIframe = () => {
    try {
        return window.self !== window.top;
    } catch (e) {
        return true;
    }
}


export interface ParcedTask {
    title: string;
    done: boolean;
}


export const App = (props: any) => {


    //const [todos, todosSetValue] = useState<TODO[]>([]);
    const [todos, todosSetValue] = useStickyState("todos", []);
    const [filterValue, setFilter] = useStickyState("filter", {
        status: 'all',
    });

    const state: State = {
        filter: filterValue,
        currentTheme: lightTheme,
    };


    useEffect(() => {
        let defaultTodos: TODO[] = [];

        const onMessageReceivedFromIframe = (event: any) => {
            const importTodos = (values: ParcedTask[]) => {
                values.forEach((value: ParcedTask, index: number) => {
                    const todo: TODO = {
                        key: Date.now().toString() + index.toString(),
                        title: value.title,
                        done: value.done,
                        newIndex: index
                    }
                    defaultTodos.push(todo);
                });
                todosSetValue(defaultTodos);
            };

            if (typeof event.data === 'object' && event.data.call === 'sendTasks') {

                //console.log('tasks', event.data.value);
                importTodos(event.data.value as ParcedTask[]);                
            }
        }

        if (inIframe()) {
            window.addEventListener("message", onMessageReceivedFromIframe);
            window.parent.postMessage({ call: 'AppIsReady' }, '*');
        }

    }, [todosSetValue]);


    // method to add a todo item to the list:
    const addTodo = (todo: TODO) => {
        // copy the state
        const newTodos: TODO[] = [...todos];
        // add my item to the copied state
        newTodos.push(todo);
        // set the state with the state API
        todosSetValue(newTodos as any);
    }


    // change a todo-item
    const changeTodo = (todo: TODO) => {
        // copy the state
        const newTodos: TODO[] = [...todos];
        // update the item in question
        const myIndex = newTodos.findIndex((item: TODO) => item.key === todo.key);

        //console.log('new state', todo.isEdit);
        //todo.isEdit = false;
        newTodos[myIndex] = todo;
        todosSetValue(newTodos as any);

    }

    const deleteTodos = () => {
        const newTodos = [...todos];
        const uncomplete = newTodos.filter((item: TODO) => item.done === false);

        todosSetValue(uncomplete as any);
    }

    const deleteTodo = (todo: TODO) => {
        const newTodos = [...todos];
        const myIndex = newTodos.findIndex((item: TODO) => item.key === todo.key)
        newTodos.splice(myIndex, 1);


        newTodos.forEach((todo: TODO, index: number) => {
            todo.newIndex = index;
        });

        todosSetValue(newTodos as any);
    }

    const changeFilter = (filterObject: Filter) => {
        // for now, I simply want to override the filter, but this could change
        //props.setState({filter: filterObject});
        setFilter(filterObject);
    }

    const applyListFilter = () => {

        switch (state.filter.status) {
            case "active":
                return todos.filter((todo: TODO) => todo.done === false);
            case "completed":
                return todos.filter((todo: TODO) => todo.done === true);
            default:
                return todos;
        }
    }

    const handleOnDragEnd = (result: DropResult) => {
        // avoid application error if item is dragged outside of the dragdropcontext
        if (!result.destination) {
            return;
        }
        // copy the state
        const reorderedTodos = [...todos];
        // obtain the todo and remove it from the array
        const [movedTodo] = reorderedTodos.splice(result.source.index, 1);
        reorderedTodos.splice(result.destination.index, 0, movedTodo);
        reorderedTodos.forEach((todo: TODO, index: number) => todo.newIndex = index);

        todosSetValue(reorderedTodos as any);

    }

    const handleOnDragUpdate = (initial: DragUpdate, provided: ResponderProvided) => {
        //console.log("new index", initial, provided);

        const newTodos: TODO[] = [...todos];

        let indexes = newTodos.map((todo: TODO, index: number) => index);

        //console.log("before", indexes);
        indexes.splice(initial.source.index, 1);
        indexes.splice(initial.destination?.index || 0, 0, initial.source.index);

        //console.log("after", indexes);

        newTodos.forEach((todo: TODO, index: number) => {
            todo.newIndex = indexes.indexOf(index);
        })
        todosSetValue(newTodos as any);
    }
  
    const getIndex = (todo: TODO, index: number): number => {
        //console.log(todo.newIndex, index);

        if (todo.newIndex >= 0) {
            return todo.newIndex;
        }
        return index;
    }

    return (
        <ThemeProvider theme={state.currentTheme}>
            <GlobalStyle theme={state.currentTheme} />
            <BackgroundHeader></BackgroundHeader>
            <TodoWrapper>
                <ListHeader />

                <InputForm addTodo={addTodo} />
                <DragDropContext onDragEnd={handleOnDragEnd} onDragUpdate={handleOnDragUpdate}>
                    <Droppable droppableId="todos" >

                        {(provided, snapshot) => (
                            <TodoList {...provided.droppableProps} ref={provided.innerRef}>
                                {applyListFilter().map((todo: TODO, index: number) =>
                                    <Draggable key={todo.key} draggableId={todo.key.toString()} index={index} /*disableInteractiveElementBlocking={!!(todo.isEdit)}*/ >
                                        {(provided, snapshot) => (
                                            <div ref={provided.innerRef} {...provided.draggableProps} {...provided.dragHandleProps}>
                                                <ToDo index={getIndex(todo, index)} todo={todo} changeTodo={changeTodo} deleteTodo={deleteTodo} />
                                            </div>)}
                                    </Draggable>)}
                                {provided.placeholder}
                            </TodoList>
                        )}
                    </Droppable>
                </DragDropContext>
                <Options filter={state.filter} changeFilter={changeFilter} todos={todos} deleteTodos={deleteTodos} />
                <Footer>
                    <p>Drag and drop to reorder list</p>
                </Footer>
            </TodoWrapper>
        </ThemeProvider>
    )

}

export default App;