import React, {useEffect} from 'react';
import styled from 'styled-components';

import 'emoji-mart/css/emoji-mart.css'
import { Picker } from 'emoji-mart'


const StyledSpanEmoji = styled.div`
@media print { display: none !important; }

color: black;
-webkit-filter: grayscale(100%);
filter: grayscale(100%);

height: 1.5em;
width: 1.5em;

display: flex;
align-items: center;

img {
    height: 20px;
    cursor: pointer;

}
`;


const StyledDivWrapperEmoji = styled.div`

width: 0;
right: 400px;
z-index: 1000;
position: absolute;
height: 0;

.wrapper {
    width: 360px;
}

`;

interface IProps {
    addEmoji(value: any): void;
    refresh: boolean;
}

export const EmojiPicker =  (props: IProps) => {
 
    useEffect(() => {
        onMouseLeave(); //children function of interest
      }, [props.refresh]);
      
    const [isShowEmoji, setEmojiState] = React.useState(false);

    const toggleEmoji = () => {
        setEmojiState(!isShowEmoji);
    }

    const onMouseLeave = () => {
        setEmojiState(false);
    }

    return (

        <div>

            <StyledSpanEmoji onClick={toggleEmoji}>	<img src={process.env.PUBLIC_URL + "/images/emoticon-30-xxl.png"} alt="" /></StyledSpanEmoji>

            {isShowEmoji ?
                <StyledDivWrapperEmoji>
                    <div className="wrapper" onMouseLeave={onMouseLeave}>
                        <Picker onSelect={props.addEmoji} showPreview={false} autoFocus={false} />
                    </div>
                </StyledDivWrapperEmoji> : null}

        </div>
    )
}